import supabase from '@app/services/supabase';
import { AuthError, Session, User } from '@supabase/supabase-js';
import {
    Context,
    createContext,
    FC,
    PropsWithChildren,
    useContext,
    useEffect,
    useLayoutEffect,
    useState,
} from 'react';
import { useNavigate } from 'react-router-dom';
import { toast } from 'react-toastify';

export interface AppUser {
    id: string;
    email?: string;
    avatarUrl?: string;
    name?: string;
    createdAt: string;
    authToken: string;
}

type AuthValueType = {
    user: AppUser | null;
    logOut: () => Promise<AuthError | null>;
    logInWithDiscord: () => Promise<AuthError | null>;
};

const AuthContext: Context<AuthValueType> = createContext({} as AuthValueType);
export const useAuth = (): AuthValueType => useContext(AuthContext);

export const appUserFromAuthSession = (session: Session): AppUser => {
    const { user, provider_token } = session;
    return {
        id: user.id,
        email: user.email,
        avatarUrl: user.user_metadata.avatar_url,
        name: user.user_metadata.full_name,
        createdAt: user.created_at,
        authToken: provider_token!,
    };
};

export const AuthProvider: FC<PropsWithChildren> = ({ children }) => {
    const [user, setUser] = useState<AppUser | null>(null);

    const updateAppUserFromSession = (session: Session | null | undefined) => {
        if (session?.user) {
            const appUser = appUserFromAuthSession(session);
            setUser(appUser);
        } else {
            setUser(null);
        }
    };
    const authValue: AuthValueType = {
        user,
        logOut: async () => {
            const { error } = await supabase.auth.signOut();
            return error;
        },
        logInWithDiscord: async () => {
            const { error } = await supabase.auth.signInWithOAuth({
                provider: 'discord',
                options: {
                    scopes: 'guilds,guilds.members.read',
                    redirectTo: process.env.PUBLIC_URL,
                },
            });

            return error;
        },
    };

    useEffect(() => {
        supabase.auth.getSession().then(({ data }) => {
            updateAppUserFromSession(data.session);
        });

        const {
            data: { subscription },
        } = supabase.auth.onAuthStateChange((_event, session) => {
            updateAppUserFromSession(session);
        });

        return () => subscription.unsubscribe();
    }, []);

    return (
        <AuthContext.Provider value={authValue}>
            {children}
        </AuthContext.Provider>
    );
};
export const useProtectedRoute = () => {
    const navigate = useNavigate();
    const { user } = useAuth();

    useLayoutEffect(() => {
        if (!user) {
            navigate('/');
            toast('You have to be logged in first', { type: 'info' });
        }
    }, []);
};
