import useQueryParams from '@app/hooks/useQueryParams';
import { useQuery } from '@tanstack/react-query';
import type { UndefinedInitialDataOptions } from '@tanstack/react-query/src/queryOptions';
import React, { ReactElement } from 'react';
import { PaginationControl } from 'react-bootstrap-pagination-control';
import { useNavigate } from 'react-router-dom';

type Props<Entity, FetchResponse = Entity[]> = {
    cachePrefix: string;
    fetchFn: (params: any) => Promise<FetchResponse>;
    children: (items: FetchResponse, isPending: boolean) => ReactElement;
    usePagination?: boolean;
    initialData?: FetchResponse;
    placeholderData?: FetchResponse;
};
export default function DataView<Entity, FetchResponse = Entity[]>({
    cachePrefix,
    fetchFn,
    children,
    usePagination = false,
    initialData,
    placeholderData,
}: Props<Entity, FetchResponse>): ReactElement {
    const navigate = useNavigate();
    const queryParams = useQueryParams();
    const page = Number(queryParams.get('page') || 1);

    const queryOptions: UndefinedInitialDataOptions<FetchResponse> = {
        queryKey: [cachePrefix, page],
        queryFn: () => fetchFn({ page }),
        retry: true,
    };

    const query = useQuery<FetchResponse>(queryOptions);
    const { isPlaceholderData, isError, data, error } = query;
    if (isError) {
        return <span>Error: {error.message}</span>;
    }
    if (!data) {
        console.log('no data');
        return <span>Loading...</span>;
    }
    return (
        <>
            {usePagination && (
                <PaginationControl
                    page={page}
                    between={4}
                    total={10}
                    limit={10}
                    next
                    changePage={newPage => {
                        navigate({
                            search: `page=${newPage}`,
                        });
                    }}
                    ellipsis={1}
                />
            )}
            {children(data, isPlaceholderData)}
        </>
    );
}
