import { Guild } from '@app/api/discord/discordEntities';
import React, { FC } from 'react';
import { Card, Image } from 'react-bootstrap';

type GuildCardProps = {
    guild: Guild;
};
const GuildCard: FC<GuildCardProps> = ({ guild }) => {
    const imgUrl = guild.icon
        ? `https://cdn.discordapp.com/icons/${guild.id}/${guild.icon}.png`
        : 'https://placehold.co/125x125?text=no+image';
    return (
        <Card>
            <Card.Body className="text-center">
                <h4>{guild.name}</h4>
                <Image src={imgUrl} loading="lazy" className="img-fluid" />
            </Card.Body>
        </Card>
    );
};

export default GuildCard;
