import { Guild } from '@app/api/discord/discordEntities';
import DataView from '@app/components/DataView';
import GuildCard from '@app/components/cards/GuildCard';
import { useAuth, useProtectedRoute } from '@app/services/AuthContext';
import axios from 'axios';
import React, { FC } from 'react';
import { Col, Row } from 'react-bootstrap';
const sleep = (seconds: number) =>
    new Promise(resolve => setTimeout(resolve, seconds * 1000));

const DashboardPage: FC = () => {
    useProtectedRoute();
    const { user } = useAuth();

    const cachePrefix = 'userGuilds';
    const fetchFn = async (params: any): Promise<Guild[]> => {
        const { data } = await axios.get<Guild[]>(
            `https://discord.com/api/v9/users/@me/guilds`,
            {
                headers: {
                    Authorization: `Bearer ${user?.authToken}`,
                },
            },
        );

        return data;
    };
    const placeholderData: Guild[] = [
        {
            id: 'x',
            name: 'x',
            owner: false,
            features: [],
            permissions: 'x',
            icon: null,
        },
    ];
    return (
        <div>
            <h1>Dashboard</h1>
            <h3>Servers</h3>
            {user?.authToken ? (
                <DataView<Guild>
                    cachePrefix={cachePrefix}
                    fetchFn={fetchFn}
                    placeholderData={placeholderData}
                >
                    {(items, isPending) => (
                        <Row xs={1} md={4} className="g-4">
                            {items.map(guild => (
                                <Col key={`challenge-card-${guild.id}`}>
                                    <GuildCard guild={guild} />
                                </Col>
                            ))}
                        </Row>
                    )}
                </DataView>
            ) : (
                <div>Fetching user credentials</div>
            )}
        </div>
    );
};

export default DashboardPage;
